import { Button, Component, Node, Vec3, _decorator } from "cc";
import { AS } from "db://as_framework/scripts//ASComponent";
import { UIDrag, UIDrop, UIPopup } from "../../extensions/as_framework/framework/assets/scripts";
const { ccclass, property } = _decorator;

@ccclass("StoryPanel")
export class StoryPanel extends AS(Component) {
  // @property({ type: SpriteFrame }) panelSlots: SpriteFrame[] = [];
  @property({ type: Node }) panel: Node = null;
  @property({ type: UIPopup }) popup: UIPopup | null = null;
  @property({ type: Node }) DragContainer: Node = null;
  @property numberOfElementPerpage: number = 4;

  private _filled = [];

  awake() {
    this._arrangeDraggable();
    this._addComponent();
  }

  _addComponent() {
    for (let i = 0; i < this.panel.children.length; i++) {
      this.panel.children[i].addComponent(UIDrop);
    }

    this.panel.addComponent(Button);
    this.panel.on(Button.EventType.CLICK, this._onButtonClick, this);

    for (let i = 0; i < this.DragContainer.children.length; i++) {
      this.DragContainer.children[i].addComponent(UIDrag);
    }

    this.popup.node.on(UIPopup.EventType.ABOUT_TO_HIDE, this._onpopuphide, this);
  }

  _arrangeDraggable() {
    let gridPositions = [
      [
        [-150, 80],
        [150, 80],
        [-150, -120],
        [150, -120],
      ],
      [
        [-150, 0],
        [150, 0],
      ],
    ];
    let gridSelector;
    if (this.numberOfElementPerpage == 2) gridSelector = 1;
    else if (this.numberOfElementPerpage == 4) gridSelector = 0;

    for (let i = 0; i < this.DragContainer.children.length; i++) {
      this.DragContainer.children[i].position = new Vec3(
        gridPositions[gridSelector][i % this.numberOfElementPerpage][0],
        gridPositions[gridSelector][i % this.numberOfElementPerpage][1],
        0,
      );
    }
  }

  _onButtonClick() {
    this.popup.show();
  }

  _onpopuphide() {
    const validDrop = function () {
      let getstatus = [];
      this.DragContainer.children.forEach((draggable, i) => {
        getstatus.push(draggable.getComponent(UIDrag).validUIDrop);
      });
      return getstatus;
    };
    console.log(validDrop);
  }
}
